<script>
export default {
  data() {
    return {
      user: "",
      username: "点击登录",
      userID: "",
      Asfe:true,
    };
  },
  computed: {},
  methods: {
    fun() {
      this.$router.push({ name: "settingup" });
    },
    //去登录
    Register(o) {
      //如果读取不到用户的信息,说明没有登录,此时跳转到登录界面
      //获取用户登录状态
      if (this.user) {
        return;
      }
      this.$router.push(o);
    }
  },
  components: {},
  created() {
    let user = JSON.parse(localStorage.getItem("user"));
    let users = JSON.parse(localStorage.getItem("users"));
    this.user = user;
    //判断用户是否存在
    if (user) {
      for (let i = 0; i < users.length; i++) {
        if (user.username == users[i].username) {
          this.username = users[i].username;
          this.userID = users[i].time;
          console.log("匹配成功");
          //获取性别
          if(user.sex){
           this.Asfe = true;
           return
          }
          this.Asfe = false;
          return;
        }
        this.username = "点击登录";
      }
      
    }
  }
};
</script>
